import Vue from "vue";
import VueRouter from "vue-router";
import Login from "../views/Login.vue";
import index from "../views/index.vue";
import index_admin from "../views/Admin/index_admin";

//
import Home from '../views/Home'
import Contact from '../views/Contact'
import News from '../views/News'
import NewsDetail from '../views/NewsDetail'
import Product from '../views/Product'
import ProductBigBoard from '../views/ProductBigBoard'
import ProductThinBoard from '../views/ProductThinBoard'
//
import Admin_Index from '../views/Admin/Admin_Index'
import Admin_Add_News from '../views/Admin/Admin_Add_News'

//
//push
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const scrollBehavior = function (to, from, savedPosition) {
  // savedPosition 
  //  router.go()  router.back() 
  //  tab   
  if (savedPosition) {
    return savedPosition
  } else {
    // 
    return { x: 0, y: 0 }
  }
}

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: index,
    children: [
      {
        path: "Login",
        name: "Login",
        component: Login,
      },
      {
        path: "",
        name: "Home",
        component: Home,
      },
      {
        path: 'Contact',
        name: 'Contact',
        component: Contact
      },
      {
        path: 'News',
        name: 'News',
        component: News
      },
      {
        path: 'NewsDetail',
        name: 'NewsDetail',
        component: NewsDetail
      },
      {
        path: 'Product',
        name: 'Product',
        component: Product
      },
      {
        path: 'ProductBigBoard',
        name: 'ProductBigBoard',
        component: ProductBigBoard
      },
      {
        path: 'ProductThinBoard',
        name: 'ProductThinBoard',
        component: ProductThinBoard
      },
    ],
  },
  {
    path: "/index_admin",
    component: index_admin,
    children: [
      {
        path: "/Admin_Add_News",
        name: "Admin_Add_News",
        component: Admin_Add_News,
      },
      {
        path: "/Admin_Index",
        name: "Admin_Index",
        component: Admin_Index,
      }
    ],
  }
];



const router = new VueRouter({
  //mode: 'history',
  //base: process.env.BASE_URL,
  scrollBehavior,
  routes,
});

export default router;
